import { Link, useLoaderData } from "remix"
import Box from "~/components/BaseKit/Box"
import { Header } from "~/components/LIsts/Header/Header"
import { getArticles } from "../../services/blogs"

export const loader = async () => {
  let articles = await getArticles()
  return articles
}

export default function Index() {
  const articles = useLoaderData()
  return (
    <Box column>
      blog
      <Box column>
        <Header
          data={[
            {
              title: "Nome",
              style: {
                textAlign: "start",
              },
            },
            {
              title: "Data",
            },
            {
              title: "Status",
            },
            {
              tutle: "Ações",
            },
          ]}
        />

        {new Array(20)
          .fill(0)
          .map((e) => articles[0])
          ?.map((e) => {
            return (
              <Box
                className="fade"
                data-scroll
                data-scroll-offset={["10%", "0%"]}
                style={{
                  height: "499px",
                  backgroundColor: "red",
                  marginBottom: "40px",
                }}
              >
                <Link key={e.id} to={`./${e.id}/editar`}>
                  {e.title}
                </Link>
              </Box>
            )
          })}
      </Box>
    </Box>
  )
}
